/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

body { font-family: "Open Sans", Helvetica, sans-serif; font-size: 16px; line-height: 1.4; background-color: #fff; }

.title { font-family: "Verdana-Bold", Helvetica, sans-serif; font-size: 60px; font-weight: 700; line-height: 1; }

.title-xs { font-family: "Open Sans", Helvetica, sans-serif; font-size: 24px; font-weight: 700; }

.inner { padding: 0 15px; margin: 0 auto; max-width: 1834px; }

.inner:after { content: " "; display: table; clear: both; }

.block { display: flex; }

.header { position: absolute; top: 0; left: 0; right: 0; padding: 0; z-index: 20; }

.header__inner { margin: 0 auto; padding: 0 30px; max-width: 1806px; }

.header__inner:after { content: " "; display: table; clear: both; }

.header__logo { float: left; margin-top: 30px; }

.header__btn-menu { float: right; }

.header__right { float: right; margin-top: 57px; }

.logo { width: 90px; vertical-align: top; display: inline-block; }

.logo img { max-width: 100%; display: block; }

.btn-menu { position: relative; margin-top: 57px; width: 47px; height: 37px; background-color: transparent; }

.btn-menu__line { position: absolute; left: 0; height: 8px; width: 100%; background-color: #FF9D11; }

.btn-menu__line:first-child { top: 0; }

.btn-menu__line:nth-child(2) { top: 50%; margin-top: -4px; }

.btn-menu__line:last-child { bottom: 0; }

.search { margin-right: 30px; }

.search__btn { font-size: 0; background-color: transparent; vertical-align: top; display: inline-block; }

.search__btn .icon { font-size: 37px; color: #ff9d11; }

.search__input { display: none; }

.nav { display: none; }

.passage { position: relative; padding: 14px 14px 14px 22px; width: 320px; text-align: left; background-color: white; overflow: hidden; border: 3px solid #0d70b7; border-radius: 10px; box-shadow: 0 4px 21px rgba(96, 96, 96, 0.26); vertical-align: top; display: inline-block; }

.passage__wrap { padding-left: 38px; overflow: hidden; display: block; }

.passage__colors { position: absolute; left: -30px; bottom: -90px; width: 150px; }

.passage__colors img { position: relative; z-index: 0; max-width: 100%; display: block; }

.passage__img { float: left; width: 33px; }

.passage__img img { position: relative; z-index: 1; max-width: 100%; display: block; }

.passage__title { font-size: 18px; color: #0d70b7; font-weight: 400; line-height: 1.1; }

.passage__link { position: relative; padding-right: 13px; color: #ff9d11; font-weight: 700; font-size: 18px; display: block; }

.no-touch .passage__link:hover span { border-color: transparent; }

.passage__link span { transition: border-color .3s; border-bottom: 1px solid; }

.passage__link .icon { position: absolute; top: 4px; right: 0; font-size: 14px; color: #0f65bb; }

.passage__link:not(:first-child) { margin-top: 20px; }

.hero { background-color: #ff9d11; }

.hero__inner { position: relative; }

.hero__content { position: absolute; top: 0; left: 0; right: 0; height: calc(100vh - 57px); text-align: center; font-size: 0; }

.hero__content:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.hero__in { padding-left: 44px; width: 100%; vertical-align: middle; font-size: 25px; text-align: left; display: inline-block; }

.hero__wrap { max-width: 425px; }

.hero__title { position: relative; z-index: 1; font-family: "Budidaya", Helvetica, sans-serif; font-size: 110px; color: #0d70b7; line-height: 1; }

.hero__hatch { position: absolute; top: -20px; left: -84px; right: -100px; bottom: -20px; width: 640px; height: 286px; z-index: -1; background-position: 0 0; background-size: cover; }

.hero__text { padding-left: 16px; font-weight: 700; line-height: 1.2; max-width: 400px; color: #636161; }

.hero__text:not(:first-child) { margin-top: 40px; }

.hero__passage { position: absolute; right: 38px; bottom: 45px; }

.hero__scroll { padding: 17px 0; text-align: center; }

.slider-hero { font-size: 0; }

.slider-hero .slick-dots { position: absolute; left: 0; right: 0; bottom: 40px; z-index: 10; text-align: center; font-size: 0; }

.slider-hero .slick-dots li { vertical-align: top; display: inline-block; }

.slider-hero .slick-dots li:not(:first-child) { margin-left: 15px; }

.slider-hero .slick-dots li.slick-active button { background-color: #fff; }

.slider-hero .slick-dots button { width: 14px; height: 14px; border-radius: 50%; font-size: 0; border: 2px solid #4069A7; background-color: #4069a7; transition: background-color .3s; display: block; }

.slider-hero__slide { position: relative; height: calc(100vh - 57px); background-position: 50% 50%; background-size: cover; }

.btn-scroll { vertical-align: top; color: #fff; font-size: 20px; line-height: 1.15; display: inline-block; }

.btn-scroll .icon { font-size: 15px; }

.video { color: #fff; font-size: 24px; background-color: #0d70b7; }

.video__row { display: flex; flex-wrap: wrap; }

.video__img-wrap { max-width: 50%; flex: 0 0 50%; }

.video__img { margin-left: -15px; min-height: 100%; background-position: 50% 50%; background-size: cover; }

.video__content { padding: 54px 0 54px 54px; width: 50%; flex: 0 0 50%; }

.video__head { display: flex; flex-wrap: wrap; }

.video__title { padding-right: 20px; max-width: 40%; flex: 0 0 40%; }

.video__text { max-width: 60%; flex: 0 0 60%; text-align: right; align-self: flex-end; }

.video__container { position: relative; height: 498px; background-position: 50% 50%; background-size: cover; display: block; }

.no-touch .video__container:hover .video__play { transform: scale(1.2); }

.video__container:not(:first-child) { margin-top: 50px; }

.video__play { position: absolute; top: 50%; left: 50%; margin: -35px 0 0 -35px; height: 70px; width: 70px; border-radius: 50%; border: 7px solid #fff; transition: transform .3s ease-in-out; }

.video__play:before { position: absolute; content: ""; top: 50%; left: 50%; margin: -9px 0 0 -5px; width: 0px; height: 0px; border-style: solid; border-width: 9px 0 9px 13px; border-color: transparent transparent transparent #fff; }

.product-page { background-color: #d88826; min-height: 100vh; display: flex; }

.product-page__layout { position: relative; max-width: 1905px; max-height: 937px; width: 100%; margin: auto; background: url(../img/product-page_bg.jpg) no-repeat 50% 50%; background-size: cover; }

.product-page__inner { max-width: 1414px; margin: 0 auto; padding: 120px 0 169px; display: flex; }

.product-page__corner-img { position: absolute; }

.product-page__corner-img img { display: block; width: 100%; }

.product-page__corner-img_left { left: 0; top: 0; max-width: 421px; max-height: 661px; }

.product-page__corner-img_right { bottom: 0; right: 0; }

.product-page__wrap { flex-basis: 50%; display: flex; flex-direction: column; align-items: flex-end; }

.product-page__details { max-width: 597px; }

.product-page__title { color: #d59744; font-size: 48px; font-family: "Montserrat", Helvetica, sans-serif; max-width: 295px; margin-bottom: 34px; text-transform: uppercase; }

.product-page__text { margin-bottom: 45px; color: #737374; font-family: "Montserrat", Helvetica, sans-serif; font-size: 18px; }

.recipe { height: 100vh; width: 100vw; display: flex; }

.recipe__image { flex: 0 0 50%; width: 50%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.recipe__content { flex: 0 0 50%; width: 50%; padding-top: 140px; }

.recipe_picture .icon { fill: #f0f; color: #f0f; }

.recipe__title { font-family: "Budidaya", Helvetica, sans-serif; font-size: 48px; font-weight: normal; color: #0d70b7; font-weight: 700; margin-left: 320px; margin-bottom: 20px; }

.recipe__ingredients, .recipe__actions { display: flex; }

.recipe__picture { text-align: center; flex: 0 0 320px; }

.recipe__icon { display: block; }

.recipe__icon .icon { font-size: 75px; color: #ce2617; }

.recipe__subtitle { font-family: "Montserrat", Helvetica, sans-serif; font-size: 24px; font-weight: 600; color: #ce2617; text-transform: uppercase; }

.recipe__ingredient { display: block; position: relative; padding-left: 24px; margin-bottom: 56px; }

.recipe__ingredient:after { content: ''; position: absolute; top: 0; bottom: 0; left: 0; width: 2px; background-color: #ff9d11; }

.recipe__measure { font-family: "Montserrat", Helvetica, sans-serif; font-size: 30px; font-weight: 600; color: #767474; }

.recipe__number { font-size: 36px; }

.recipe__product { font-family: "Montserrat", Helvetica, sans-serif; font-size: 24px; color: #2e7dbb; font-weight: 600; }

.recipe__actions-list { list-style-type: decimal; max-width: 540px; }

.recipe__action { font-family: "Arial", sans-serif; font-size: 24px; font-weight: normal; font-style: normal; font-stretch: normal; line-height: 1.5; letter-spacing: normal; text-align: left; color: #6b6b6b; padding-left: 10px; }
